import { Injectable } from '@angular/core';
import { Headers, Http } from '@angular/http'

import 'rxjs/add/operator/toPromise';

import { Hero } from '../interfaces/hero';
import { Pagination } from '../interfaces/pagination';

@Injectable()
export class HeroService {
  private headers = new Headers({ 'Content-Type': 'application/json' });
  // private heroesApiUrl = 'http://localhost:9000/api/demo/heroes';
  private heroesApiUrl = 'https://play-home.herokuapp.com/api/demo/heroes';


  constructor(private http: Http) { }

  getHeroes(pagination: Pagination): Promise<any> {
    var url: string = `${this.heroesApiUrl}`;
    url += `?pageIndex=${pagination.pageIndex}`;
    url += `&pageSize=${pagination.pageSize}`;
    url += `&orderField=${pagination.orderField}`;
    url += `&orderDirection=${pagination.orderDirection}`;
    url += `&queryString=${pagination.queryString}`;

    return this.http.get(url)
      .toPromise()
      .then(response => {
        return response.json();
      })
      .catch(this.handleError);
  }

  getHero(id: number): Promise<Hero> {
    const url = `${this.heroesApiUrl}/${id}`;
    return this.http.get(url)
      .toPromise()
      .then(response => {
        return response.json().item as Hero;
      })
      .catch(this.handleError);
  }

  create(hero: Hero): Promise<Hero> {
    const url = `${this.heroesApiUrl}`;
    return this.http
      .post(url, JSON.stringify(hero), { headers: this.headers })
      .toPromise()
      .then(() => hero)
      .catch(this.handleError);
  }

  update(hero: Hero): Promise<any> {
    const url = `${this.heroesApiUrl}/${hero.id}`;
    return this.http
      .put(url, JSON.stringify(hero), { headers: this.headers })
      .toPromise()
      .then(response => {
        return response.json();
      })
      .catch(this.handleError);
  }

  delete(hero: Hero): Promise<Hero> {
    const url = `${this.heroesApiUrl}/${hero.id}`;
    return this.http
      .delete(url)
      .toPromise()
      .then(() => hero)
      .catch(this.handleError);
  }

  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }
}



