import 'rxjs/add/operator/switchMap';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { Location }               from '@angular/common';

import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { ConfirmModalComponent } from '../modals/confirm-modal/confirm-modal.component'

import { HeroService} from '../services/hero.service';
import { Alert } from '../interfaces/alert';
import { Hero } from '../interfaces/hero';

@Component({
  selector: 'app-hero-detail',
  templateUrl: './hero-detail.component.html',
  styleUrls: ['./hero-detail.component.css']
})
export class HeroDetailComponent implements OnInit {

  private sub: any;
  id: number;
  hero: Hero;
  alerts: Alert[];

  constructor(
    private route: ActivatedRoute,
    private heroService: HeroService,
    private location: Location,
    private modalService: NgbModal) { }

  ngOnInit(): void {
    this.route.params
      .switchMap((params: Params) => this.heroService.getHero(+params['id']))
      .subscribe(hero => this.hero = hero);
  }

  getHero(id: number): void {
    this.heroService.getHero(id).then(hero => this.hero = hero);
  }

  create(): void {
    console.log(this.hero);
    this.heroService.create(this.hero).then(() => this.goBack());
  }

  update(): void {
    const modalRef = this.modalService.open(ConfirmModalComponent);
    modalRef.componentInstance.message = '確認修改「' + this.hero.name + '」的資料？';
    modalRef.result.then((result) => {
      //　確認修改
      //this.heroService.update(this.hero).then(() => this.goBack());
      this.heroService.update(this.hero).then(data => {
        this.alerts = data.alerts as Alert[];
      });

    }, (reason) => {
      console.log("cancel-" + reason);
      //取消修改
    });
  }

  delete(): void {
    const modalRef = this.modalService.open(ConfirmModalComponent);
    modalRef.componentInstance.message = '確認刪除「' + this.hero.name + '」的資料？';
    modalRef.result.then((result) => {
      //　確認修改
      this.heroService.delete(this.hero).then(() => this.goBack());
    }, (reason) => {
      console.log("cancel-" + reason);
      //取消修改
    });
  }

  goBack(): void {
    this.location.back();
  }

}



