import { Component, OnInit } from '@angular/core';

import { HeroService} from '../services/hero.service';

import { Alert } from '../interfaces/alert';
import { Hero } from '../interfaces/hero';
import { Pagination } from '../interfaces/pagination';

@Component({
  selector: 'app-heroes',
  templateUrl: './heroes.component.html',
  styleUrls: ['./heroes.component.css']
})

export class HeroesComponent implements OnInit {

  heroes: Hero[];
  alerts: Alert[];
  pagination: Pagination = {
    pageSize: 10,
    pageIndex: 1,
    totalRowCount: 0,
    orderField: '',
    orderDirection: '',
    queryString: '',
  };

  constructor(private heroService: HeroService) { }

  ngOnInit() {
    this.getHeroes();
  }

  getHeroes(): void {
    //this.heroService.getHeroes().then(heroes => this.heroes = heroes);
    this.heroService.getHeroes(this.pagination).then(data => {
      this.alerts = data.alerts as Alert[];
      this.heroes = data.items as Hero[];
      this.pagination.totalRowCount = data.totalRowCount as number;
      this.pagination.pageIndex = data.pageIndex as number;
    });
  }

  onPageChange(): void {
    this.getHeroes();
  }

  onSortChange(orderField: string, orderDirection: string): void {
    this.pagination.orderField = orderField;
    this.pagination.orderDirection = orderDirection;
    this.getHeroes();
  }

  onSearch(keyword): void {
    this.pagination.queryString = "keyword:" + keyword;
    this.getHeroes();
  }

  onSearchBoxKeyup(event, keyword): void {
    if (event.keyCode != 13) return;
    this.onSearch(keyword);
  }

}



